@font-face {
  font-family: 'leaner';
  src: url(Leaner-Thin.ttf);
}
@font-face {
  font-family: 'baufra';
  src: url(Baufra-Medium.ttf);
}
@font-face {
  font-family: 'mars';
  src: url(MarsMission-Ajpg.ttf);
}
#crypto-data {
  display: flex;
  align-items: center;
  justify-content: center;
}
#title-div {
  position: fixed;
  background-image: url('moon.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  color: white;
  text-align: center;
  z-index: 100;
  width: 100%;
  height: 150px;
  top:0;
  border-bottom: 2px solid black;
  font-family: 'baufra';
}
#history {
  position: absolute;
  top:0;
  left:25%;
}
.line-graph {
  position: relative;
  top: 2.5%;
  left: 2%;
  grid-column: 12 / 16;
  width: 355px;
  background-color: white;
}
.history-graph {
  position: relative;
  top:-205%;
  left: 5%;
  grid-column: 7 / 12;
  width: 355px;
  background-color: white;
}
.description-div{
  position: relative;
  top: -360px;
  left: 2.5%;
  grid-column: 7 / 17;
  width: 725px;
  height: 190px;
  background-color: white;
  font-size: 10px;
  overflow-y: scroll;
  font-family: 'baufra';
}
.coin-div {
  margin: 2px;
  box-shadow: 1px 1px 1px black, -1px -1px 1px black;;
  padding: 5px;
  border-radius: 2px;
}
.coin-data {
  list-style-type: none;
  position: relative;
  margin-right: 15px;
}
.coin-data-title {
  font-size: 18px;
}
.coin-data-title:hover {
  text-shadow:  1px 1px 1px white,-1px 1px 1px white,1px -1px 1px white,-1px -1px 1px white;
  color: black;

}
.collapse-btn {
  position: relative;
  top:25%;
  font-size: 15px;
  display: grid;
  text-align: center;
  grid-template-columns: repeat(8, 1fr);
}
.collapse-btn-title {
 left:11%;
 width:79%;
}
#coin-list {
  font-family: 'leaner';
  font-size: 25px;
  font-weight: 900;
  position: relative;
  top: 153px;
  width:100%;
  align-items: center;
  list-style-type: none;
}
.rank {
  grid-column: 1 / 2;
  position: relative;
}
.coin-name {
  text-align: left;
  grid-column: 2 / 3;
  font-family: 'baufra';
  width: 125%;
}
.price {
  grid-column: 3 / 4;
}
.marketcap {
  grid-column: 4 / 5;
}
.dominance {
  grid-column: 5 / 6;
}
.supply {
  grid-column:  6 / 7;
}
.max-supply {
  grid-column: 7 / 8;
}
.change24 {
  grid-column: 8 / 9;
}

#total-market-cap {
  font-size: 18px;
  font-weight: bolder;
}
.negative {
  color: red;
}
.positive {
  color: green;
}
.negative-box {
  box-shadow: inset 0 0 0 1px red, 1px 1px 1px red, -1px -1px 1px red;
}
.positive-box {
  box-shadow: inset 0 0 0 1px green, 1px 1px 1px green, -1px -1px 1px green;
}
#list-div{
  width:100vw;
  height: 100vh;
  display:flex;
  justify-content: center;
}
#coin-news,
#coin-info {
  position: relative;
  top: 20px;
  left: 10px;
  border:1px solid black;
  border-top: none;
  width: 100%;
  height: 370px;
  display:grid;
  grid-template-rows: repeat(10,1fr);
}
#coin-info {
  grid-column: 4 / 7;

}
#coin-news {
grid-column: 1 / 4;
width: 100%;
overflow-y:scroll;
}
.collapse {
  height: 400px;
  overflow: hidden;
}
.collapse-inside {
  display: grid;
  grid-template-columns: repeat(16, 1fr);
  gap: 5px;
}
#coin-news-title,
#coin-info-title{
  position: relative;
  grid-row: 1 / 2;
  grid-column: 1 / 4;
  text-align: center;
  text-decoration: underline;
}
#coin-news-title {
  background-color: white;
}
.coin-items {
  text-align: center;
  font-size: 18px;;
}
.item1 {
  grid-row: 2 / 3;
  grid-column: 1 / 4;
}
.item2 {
  grid-row: 3 / 4;
  grid-column: 1 / 4;
}
.item3 {
  grid-row: 4 / 5;
  grid-column: 1 / 4;
}
.item4 {
  grid-row: 5 / 6;
  grid-column: 1 / 4;
}
.item5 {
  grid-row: 6 / 7;
  grid-column: 1 / 4;
}
.item6 {
  grid-row: 7 / 8;
  grid-column: 1 / 4;
}
.item7 {
  grid-row: 8 / 9;
  grid-column: 1 / 4;
}
.item8 {
  grid-row: 9 / 10;
  grid-column: 1 / 4;
}
.item9 {
  grid-row: 10 / 11;
  grid-column: 1 / 4;
}
.coin-thumbnail {
  position: relative;
  max-height: 20px;
}
#graph-div {
  position: relative;
  height: 100%;
}
.graph {
  position: relative;
  height:100px;
}
#coin-data-div {
  width: 80%;
  height: 234.5%;
}
#coin-placement-div {
  position: relative;
  width: 102.6%;
  left: -2.6%;
}
.news-title {
  position: relative;
  top: 10px;
  font-size: 13px;
  width: 100%;
  border-bottom: 1px solid black;
  text-align: center;
  margin: 0px;
}
.bolder {
  font-weight: 900;
}
::-webkit-scrollbar {
  width: 0;
}
